.task {
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 20px;
  margin: 10px 0;
  background-color: #f9f9f9;
  transition: transform 0.5s, opacity 0.5s;
}

.task-enter {
  opacity: 0;
  transform: scale(0.9);
}

.task-enter-active {
  opacity: 1;
  transform: scale(1);
}

.task-exit {
  opacity: 1;
  transform: scale(1);
}

.task-exit-active {
  opacity: 0;
  transform: scale(0.9);
}

.progress-bar-background {
  width: 100%;
  background-color: #e0e0e0;
  border-radius: 5px;
  overflow: hidden;
  margin: 10px 0;
  position: relative;
}

.progress-bar {
  height: 20px;
}

.progress-text {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  color: white;
  font-weight: bold;
}

input[type="range"] {
  width: 100%;
}

.category {
  font-size: 14px;
  color: #7f8c8d;
  margin-top: -10px;
  margin-bottom: 10px;
}

.category-labels {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 10px;
}

.category-label {
  background-color: #3498db;
  color: white;
  padding: 5px 25px 5px 10px;
  /* Right padding for icon */
  border-radius: 15px;
  margin-right: 5px;
  margin-bottom: 5px;
  display: inline-flex;
  align-items: center;
  font-size: 14px;
  position: relative;
  max-width: 100%;
  /* Ensure it doesn't exceed container width */
}

.category-label-text {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  margin-right: 20px;
  /* Space for the remove icon */
}

.remove-category {
  background: none;
  border: none;
  color: white;
  cursor: pointer;
  font-size: 14px;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  transition: background-color 0.3s;
  position: absolute;
  right: 5px;
  top: 50%;
  transform: translateY(-50%);
}

.remove-category:hover {
  background-color: rgba(255, 255, 255, 0.2);
}

.category-section {
  margin-bottom: 15px;
}

.category-input {
  display: flex;
  align-items: center;
}

.category-input input {
  flex-grow: 1;
  padding: 10px;
  border: 1px solid #bdc3c7;
  border-radius: 5px;
  font-size: 14px;
  margin-right: 10px;
}

.category-input input:focus {
  outline: none;
  border-color: #3498db;
  box-shadow: 0 0 0 2px rgba(52, 152, 219, 0.2);
}

.category-input button {
  background-color: #3498db;
  color: white;
  border: none;
  padding: 10px 15px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
  font-size: 14px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.add-icon {
  font-size: 18px;
  font-weight: bold;
}

.category-input button:hover {
  background-color: #2980b9;
}

@media (max-width: 768px) {
  .task {
    padding: 15px;
  }
  .category-input {
    flex-direction: column;
  }
  .category-input input {
    margin-right: 0;
    margin-bottom: 10px;
  }
  .category-input button {
    width: 100%;
  }
}

@media (max-width: 576px) {
  .category-label {
    font-size: 12px;
  }
  .remove-category {
    width: 16px;
    height: 16px;
  }
}

.task-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.task-header h3 {
  margin: 0;
  flex-grow: 1;
}

.drag-handle {
  position: absolute;
  top: 10px;
  right: 10px;
  width: 20px;
  height: 20px;
  cursor: grab;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.drag-handle div {
  width: 100%;
  height: 3px;
  background-color: #bdc3c7;
}

.progress-section {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.progress-bar-container {
  flex-grow: 1;
  height: 20px;
  background-color: #ecf0f1;
  border-radius: 10px;
  overflow: visible;
  margin-right: 10px;
  position: relative;
}

.progress-bar {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: width 0.3s ease;
  border-radius: 10px;
}

.progress-text {
  color: white;
  font-weight: bold;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
  z-index: 2;
}

.progress-handle-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.progress-handle {
  width: 16px;
  height: 30px;
  background-color: #fff;
  border: 2px solid #3498db;
  border-radius: 4px;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
  cursor: pointer;
  z-index: 3;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
  pointer-events: auto;
}

.progress-input {
  width: 60px;
  padding: 5px;
  font-size: 14px;
  border: 1px solid #bdc3c7;
  border-radius: 5px;
  margin-left: 10px;
}

.control-bar-container {
  height: 10px;
  background-color: #ecf0f1;
  border-radius: 5px;
  margin-top: 5px;
  margin-bottom: 10px;
  position: relative;
  cursor: pointer;
  z-index: 1;
  /* Lower than modal */
}

.control-bar-progress {
  height: 100%;
  background-color: #3498db;
  border-radius: 5px;
  transition: width 0.1s ease;
}

.control-bar-handle {
  width: 20px;
  height: 20px;
  background-color: #fff;
  border: 2px solid #3498db;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  cursor: pointer;
  z-index: 2;
  /* Lower than modal */
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
  user-select: none;
}

/* Add Category input field styles */

.category-section form input[type="text"] {
  padding: 5px;
  font-size: 14px;
  border: 1px solid #bdc3c7;
  border-radius: 5px;
  margin-right: 5px;
}

.category-section form button {
  padding: 5px 10px;
  font-size: 14px;
  background-color: #3498db;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.category-section form button:hover {
  background-color: #2980b9;
}

/* ... other styles ... */

/* Modal styles */

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 999;
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal {
  background-color: white;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  z-index: 1000;
  max-width: 90%;
  width: 400px;
  border-radius: 8px;
}

/* ... existing styles ... */

.progress-bar {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: width 0.3s ease;
}

.progress-text {
  color: white;
  font-weight: bold;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
}

.progress-bar.low-progress .progress-text {
  color: #2c3e50;
  /* Dark color for low progress */
}

/* Drag and drop handle styles */

.drag-handle {
  position: absolute;
  top: 10px;
  right: 10px;
  width: 20px;
  height: 20px;
  cursor: grab;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.drag-handle div {
  width: 100%;
  height: 3px;
  background-color: #bdc3c7;
}

.task-item.dragging {
  opacity: 0.5;
  border: 2px dashed #3498db;
}

.drag-handle:active {
  cursor: grabbing;
}
